.taxonomy_container {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  border: 0.75px solid lightgray;
  align-items: center;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  width: 1040px;
}

/* Four Style Sections: Start (Thesauri), Resultslist, TermInfo, Footer */

/* ------- Start (Thesauri or SearchBar) view ------- */
.taxonomy_container .center {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  text-align: center;
  margin-bottom: 15px;
}

.taxonomy_container .center button {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  line-height: 28px;
  height: 42px;
  padding-left: 5px;
  padding-right: 5px;
  margin-left: 5px;
  background-color: white;
  border: 2px solid #757575;
  color: black;
}

.taxonomy_container .center button a {
  text-decoration: none;
}

.taxonomy_container .center button:hover {
  border: 2px solid black;
}

.taxonomy_container .center input[type=text] {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  display: inline-block;
  /* vertical-align: top; */
  margin-left: 5px;
  margin-top: 0;
  border: 2px solid #757575;
  width: 250px;
  font-size: 16px;
  padding-left: 5px;
  padding-right: 5px;
  height: 36px;
}

.taxonomy_container .center input[type=text]::-webkit-input-placeholder {
  color: #515151;
}

.taxonomy_container .center input[type=text]::-ms-input-placeholder {
  color: #515151;
}

.taxonomy_container .center input[type=text]::placeholder {
  color: #515151;
}

.taxonomy_container .center input[type=text]:hover {
  border: 2px solid darkgray;
}

.taxonomy_container .dropdown {
  width: 200px;
  height: 42px;
  font-family: 'Lato', 'Helvetica Neue', 'Helvetica', 'Arial', 'sans-serif';
  text-align: left;
  padding-left: 5px;
  font-size: 16px;
  background-color: white;
  border: 2px solid #757575;
  border-radius: 0px;  
  -webkit-appearance: none;  
     -moz-appearance: none;  
          appearance: none;
  background: url(../../static/media/dropdown-icon.0dadb5de.svg) 95% / 15% no-repeat;
}

select::-ms-expand {
  display: none;
}

.taxonomy_container .dropdown:hover {
  border: 2px solid darkgray;
}

.taxonomy_container .categoryList {
  text-align: left;
  margin-left: 212px;
}

.taxonomy_container .categoryList > p {
  margin-left: 40px;
  margin-bottom: 0;
}

.taxonomy_container .categoryList > ul > li {
  display: inline;
  list-style-type: none;
  padding-right: 25px;
}

/* ------- ResultsList view ------- */

.taxonomy_container .resultsList {
  margin-top: 20px;
}

.taxonomy_container .resultsList li {
  list-style-type: none;
  padding-bottom: 3px;
}

.taxonomy_container .spinner {
  margin-left: 75px;
}

.taxonomy_container ul.pagination li {
  display: inline-block;
  text-align: center;
  width: 20px;
  line-height: 24px;
  margin: 3px 7px;
}

.taxonomy_container ul.pagination li.active {
  border-bottom: 2px solid black;
}

.taxonomy_container ul.pagination li a {
  color: black;
  text-decoration: none;
}

/* ------- TermInfo view ------- */

.taxonomy_container .breadcrumbs {
  margin: 25px 20px 20px auto;
  display: inline-block;
}

.taxonomy_container .breadcrumbs h1, h2, h3, h4 {
  display: inline;
}

.taxonomy_container .termInfo span {
  display: block;
  background-color: #e8eff3;
  padding: 10px;
}

.taxonomy_container .termInfo p {
  margin-left: 20px;
}

.taxonomy_container hr {
  border: none;
  height: 3px;
  background-color: #94afbf;
  box-shadow: 0 1px 1px 0 gray;
}

.taxonomy_container .termRelation span {
  display: block;
  background-color: #e8eff3;
  padding: 10px;
}

.taxonomy_container .termRelation div {
  display: inline-block;
  vertical-align: top;
  margin: 5px 10px 5px 20px;
}

.taxonomy_container .termRelation p {
  margin: 15px auto 5px;
}

.taxonomy_container .termRelation li {
  list-style-type: none;
  margin-bottom: 3px;
}

.taxonomy_container .termRelation ul {
  padding-left: 0;
}


.taxonomy_container .termRelation img {
  margin-top: 10px;
}

.taxonomy_container .superTerms {
  margin: 15px auto;
}

.taxonomy_container .superTerms p {
  margin: 5px auto 5px 20px;
}

.taxonomy_container .superTerms ul {
  padding-left: 0;
  margin-left: 20px;
}

.taxonomy_container .superTerms li {
  list-style-type: none;
}

.taxonomy_container .newSearch {
  float: right;
  margin: 15px 0px 0px 15px;
}

/* ------- FloatingSearchBox on ResultsList and TermInfo views ------- */

.taxonomy_container .newSearch input {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  display: inline-block;
  /* vertical-align: top; */
  margin-left: 5px;
  margin-top: 0;
  border: 2px solid #757575;
  width: 200px;
  font-size: 16px;
  padding-left: 5px;
  padding-right: 5px;
  height: 28px;
}

.taxonomy_container .newSearch input[type=text]:hover {
  border: 2px solid darkgray;
}

.taxonomy_container .newSearch button {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  /* line-height: 28px; */
  height: 34px;
  padding-left: 5px;
  padding-right: 5px;
  margin-left: 5px;
  background-color: white;
  border: 2px solid #757575;
  color: black;
}

.taxonomy_container .newSearch button > a {
  text-decoration: none;
}

.taxonomy_container .newSearch button:hover {
  border: 2px solid black;
}

/* Footer (appears in ResultsList and TermInfo views) */

.taxonomy_container pre {
  color: red;
  font-size: 14px;
  padding: 4px 8px;
  background: #f8f8f8;
  border-radius: 5px;
  border: 1px solid #e5e5e5;
  overflow-x: auto;
}

.taxonomy_container .footer {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  margin-top: 20px;
}

.taxonomy_container .footer button {
  border: 2px solid #757575;
  background-color: white;
  font-size: 16px;
  line-height: 20px;
  margin-top: 8px;
  padding-top: 1px;
}

.taxonomy_container .footer a[download] {
  font-size: 16px;
  line-height: 20px;
  margin-left: 3px;
  text-decoration: none;
  border: 2px solid #757575;
  background-color: white;
  color: black;
  padding: 1px 7px 3px 7px;
}

.taxonomy_container .footer button:hover {
  border: 2px solid black;
}

.taxonomy_container .footer a[download]:hover {
  border: 2px solid black;
}

.taxonomy_container .footer a[download] img {
  height: 15px;
  position: relative;
  top: 1px;
}
.react-spinner-loader-swing div {
  border-radius: 50%;
  float: left;
  height: 1em;
  width: 1em;
}
.react-spinner-loader-swing div:nth-of-type(1) {
  background: -webkit-linear-gradient(left, #385c78 0%, #325774 100%);
  background: linear-gradient(to right, #385c78 0%, #325774 100%);
}
.react-spinner-loader-swing div:nth-of-type(2) {
  background: -webkit-linear-gradient(left, #325774 0%, #47536a 100%);
  background: linear-gradient(to right, #325774 0%, #47536a 100%);
}
.react-spinner-loader-swing div:nth-of-type(3) {
  background: -webkit-linear-gradient(left, #4a5369 0%, #6b4d59 100%);
  background: linear-gradient(to right, #4a5369 0%, #6b4d59 100%);
}
.react-spinner-loader-swing div:nth-of-type(4) {
  background: -webkit-linear-gradient(left, #744c55 0%, #954646 100%);
  background: linear-gradient(to right, #744c55 0%, #954646 100%);
}
.react-spinner-loader-swing div:nth-of-type(5) {
  background: -webkit-linear-gradient(left, #9c4543 0%, #bb4034 100%);
  background: linear-gradient(to right, #9c4543 0%, #bb4034 100%);
}
.react-spinner-loader-swing div:nth-of-type(6) {
  background: -webkit-linear-gradient(left, #c33f31 0%, #d83b27 100%);
  background: linear-gradient(to right, #c33f31 0%, #d83b27 100%);
}
.react-spinner-loader-swing div:nth-of-type(7) {
  background: -webkit-linear-gradient(left, #da3b26 0%, #db412c 100%);
  background: linear-gradient(to right, #da3b26 0%, #db412c 100%);
}
.react-spinner-loader-shadow {
  clear: left;
  padding-top: 1.5em;
}
.react-spinner-loader-shadow div {
  -webkit-filter: blur(1px);
  filter: blur(1px);
  float: left;
  width: 1em;
  height: .25em;
  border-radius: 50%;
  background: #e3dbd2;
}
.react-spinner-loader-shadow .react-spinner-loader-shadow-l {
  background: #d5d8d6;
}
.react-spinner-loader-shadow .react-spinner-loader-shadow-r {
  background: #eed3ca;
}
@-webkit-keyframes ball-l {
  0%, 50% {
    -webkit-transform: rotate(0) translateX(0);
    transform: rotate(0) translateX(0);
  }
  100% {
    -webkit-transform: rotate(50deg) translateX(-2.5em);
    transform: rotate(50deg) translateX(-2.5em);
  }
}
@keyframes ball-l {
  0%, 50% {
    -webkit-transform: rotate(0) translate(0);
    transform: rotate(0) translateX(0);
  }
  100% {
    -webkit-transform: rotate(50deg) translateX(-2.5em);
    transform: rotate(50deg) translateX(-2.5em);
  }
}
@-webkit-keyframes ball-r {
  0% {
    -webkit-transform: rotate(-50deg) translateX(2.5em);
    transform: rotate(-50deg) translateX(2.5em);
  }
  50%,
  100% {
    -webkit-transform: rotate(0) translateX(0);
    transform: rotate(0) translateX(0);
  }
}
@keyframes ball-r {
  0% {
    -webkit-transform: rotate(-50deg) translateX(2.5em);
    transform: rotate(-50deg) translateX(2.5em);
  }
  50%,
  100% {
    -webkit-transform: rotate(0) translateX(0);
    transform: rotate(0) translateX(0)
  }
}
@-webkit-keyframes shadow-l-n {
  0%, 50% {
    opacity: .5;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    opacity: .125;
    -webkit-transform: translateX(-1.57em);
    transform: translateX(-1.75em);
  }
}
@keyframes shadow-l-n {
  0%, 50% {
    opacity: .5;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    opacity: .125;
    -webkit-transform: translateX(-1.75);
    transform: translateX(-1.75em);
  }
}
@-webkit-keyframes shadow-r-n {
  0% {
    opacity: .125;
    -webkit-transform: translateX(1.75em);
    transform: translateX(1.75em);
  }
  50%,
  100% {
    opacity: .5;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}
@keyframes shadow-r-n {
  0% {
    opacity: .125;
    -webkit-transform: translateX(1.75em);
    transform: translateX(1.75em);
  }
  50%,
  100% {
    opacity: .5;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}
.react-spinner-loader-swing-l {
  -webkit-animation: ball-l .425s ease-in-out infinite alternate;
  animation: ball-l .425s ease-in-out infinite alternate;
}
.react-spinner-loader-swing-r {
  -webkit-animation: ball-r .425s ease-in-out infinite alternate;
  animation: ball-r .425s ease-in-out infinite alternate;
}
.react-spinner-loader-shadow-l {
  -webkit-animation: shadow-l-n .425s ease-in-out infinite alternate;
  animation: shadow-l-n .425s ease-in-out infinite alternate;
}
.react-spinner-loader-shadow-r {
  -webkit-animation: shadow-r-n .425s ease-in-out infinite alternate;
  animation: shadow-r-n .425s ease-in-out infinite alternate;
}


.react-spinner-loader-svg svg {
  -webkit-transform-origin: 50% 65%;
          transform-origin: 50% 65%;
}

.react-spinner-loader-svg svg polygon {
  stroke-dasharray: 17;
  -webkit-animation: dash 2.5s cubic-bezier(0.35, 0.04, 0.63, 0.95) infinite;
          animation: dash 2.5s cubic-bezier(0.35, 0.04, 0.63, 0.95) infinite;
}

@-webkit-keyframes dash {
  to {
    stroke-dashoffset: 136;
  }
}

@keyframes dash {
  to {
    stroke-dashoffset: 136;
  }
}
@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.react-spinner-loader-svg-calLoader {
  width: 230px;
  height: 230px;
  -webkit-transform-origin: 115px 115px;
          transform-origin: 115px 115px;
  -webkit-animation: 1.4s linear infinite loader-spin;
          animation: 1.4s linear infinite loader-spin;
}

.react-spinner-loader-svg-cal-loader__path {

  -webkit-animation: 1.4s ease-in-out infinite loader-path;

          animation: 1.4s ease-in-out infinite loader-path;
}

@-webkit-keyframes loader-spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loader-spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes loader-path {
  0% {
    stroke-dasharray: 0, 580, 0, 0, 0, 0, 0, 0, 0;
  }
  50% {
    stroke-dasharray: 0, 450, 10, 30, 10, 30, 10, 30, 10;
  }
  100% {
    stroke-dasharray: 0, 580, 0, 0, 0, 0, 0, 0, 0;
  }
}
@keyframes loader-path {
  0% {
    stroke-dasharray: 0, 580, 0, 0, 0, 0, 0, 0, 0;
  }
  50% {
    stroke-dasharray: 0, 450, 10, 30, 10, 30, 10, 30, 10;
  }
  100% {
    stroke-dasharray: 0, 580, 0, 0, 0, 0, 0, 0, 0;
  }
}

